<template>
  <v-card flat>
    <v-card-title class="d-flex align-center pe-2">
      <v-icon icon="mdi-video-input-component"></v-icon> &nbsp;
      `25.3월 야근현황

      <v-spacer></v-spacer>

      <v-text-field
        v-model="search"
        density="compact"
        label="Search"
        prepend-inner-icon="mdi-magnify"
        variant="solo-filled"
        flat
        hide-details
        single-line
      ></v-text-field>
    </v-card-title>

    <v-divider></v-divider>
    <v-data-table
      v-model:search="search"
      :filter-keys="['name']"
      :items="targetOrg"
      :headers="headers"
      density
      style="width: 100%;"
      :items-per-page="50"
      :items-per-page-options="[20, 50, 100]"
      class="tableHead"
    >
      <template v-slot:header.stock>
        <div class="text-end">Stock</div>
      </template>

      <template v-slot:item.image="{ item }">
        <v-card class="my-2" elevation="2" rounded>
          <v-img
            :src="`https://cdn.vuetifyjs.com/docs/images/graphics/gpus/${item.image}`"
            height="64"
            cover
          ></v-img>
        </v-card>
      </template>

      <template v-slot:item.rating="{ item }">
        <v-rating
          :model-value="item.rating"
          color="orange-darken-2"
          density="compact"
          size="small"
          readonly
        ></v-rating>
      </template>

      <template v-slot:item.stock="{ item }">
        <div class="text-end">
          <v-chip
            :color="item.stock ? 'green' : 'red'"
            :text="item.stock ? 'In stock' : 'Out of stock'"
            class="text-uppercase"
            size="small"
            label
          ></v-chip>
        </div>
      </template>
    </v-data-table>
  </v-card>
</template>


<script>
  import OTstatus from '../assets/marchOT.json';
  import { ref } from 'vue'
  import { mapGetters, mapActions } from 'vuex'
  import OrgArray from '../assets/org.json';
  import auth from '../assets/auth.json';

  export default {
    mounted(){
      this.checkLogIn()
      this.filterByAuth()
      
    },
    computed: {
    ...mapGetters(['getTargetOrg','getUserInfo']),
    targetOrg(){
      const arr=OTstatus.filter(org => org.조직===this.getTargetOrg)
      function sortByAverage(arr){
        return arr.sort((obj1, obj2)=> obj2.twentyfiveAverage - obj1.twentyfiveAverage)
      }
      const sortedArr=sortByAverage(arr).map((org, i)=>({
        ...org,
        index: i+1
      }))
      return sortedArr
    }
    },
    data(){
      return{
        search: ref(''),
        headers: [
          {title:"#", key:"index", width: "1%", minWidth: "10px"},
          {title:"구분",key:"구분", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"`25 누계평균", key:"twentyfiveAverage", align:"center", width: "12.4%", minWidth: "90px"},
          {title:"목표대비", key:"gap", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"3개월 평균", key:"threeAverage", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"3월", key:"thisMonth", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"2월", key:"beforeMonth", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"1월", key:"twoBeforeMonth", align:"center", width: "12.4%", minWidth: "80px"},
          {title:"`24 평균", key:"24average", align:"center", width: "12.4%", minWidth: "80px"}
        ]
      }
    },
    methods: {
      ...mapActions(['initializeSections','updateTargetOrg']),
      getFirstElement(input) { // 입력 값이 배열인지 확인
        if (Array.isArray(input)) {
          return input[0]; // 배열이면 첫 번째 요소 반환
        }
        return input; // 배열이 아니면 그대로 반환
      },
      checkLogIn(){ // 로그인안되면 홈으로 돌아가기 

        function isIncludesIn(target, arr) { // 배열 b를 생성하여 객체의 "mail" 값을 저장
          const allMyUsers = arr.map(obj => obj["mail"]);
          console.log(target, allMyUsers)
          const result = allMyUsers.includes(target);
          return result;
        }
        const isMyUser = isIncludesIn(this.getUserInfo, auth)
          if(!isMyUser){
            alert("권한이 없습니다. 관리자에게 문의하세요")
             window.location.href = "/new.io/";
          } else{
            const targetAuth = auth.find(obj => obj.mail === this.getUserInfo)
            const firstViewTargetOrg = this.getFirstElement(targetAuth.allowedGroup)
            this.updateTargetOrg(firstViewTargetOrg)
            return  
          }
      },
    filterByAuth(){
      const userInfo = this.getUserInfo
      
      const allowedGroup = this.getDataByCondition(auth, userInfo)

      const manuArr = [{"category":"연장근로 현황",
        "id":1,
        "isOpen":true,
        "rules": []
      }]

      manuArr[0].rules = allowedGroup
      
      this.$store.dispatch('initializeSections', manuArr);
    },

    getDataByCondition(arr, user) {
      // 조건: conditionObj가 { id: "all" }을 포함하면 전체 배열 반환
      const targetAuth = arr.find(obj => obj.mail === user)
      console.log(targetAuth)
      const allowedGroup = targetAuth.allowedGroup
      console.log(allowedGroup)
      if (allowedGroup[0] === "전사") {
        console.log("전사 케이스")
        const arrangedGroup = OrgArray[0].rules.map((org, i) => ({ id: i+1, 구분: org.구분 })) 
        arrangedGroup[0].isOpen = true;
        return arrangedGroup
      } else {
        return allowedGroup.map((org, i) => ({id: i+1, 구분: org })) 
      } 
    },    
  }
  }
</script>

<style>
 .v-table > .v-table__wrapper > table{
    width: 90%;
 }
 .v-table table th {
    font-size: 10px;
 }
 .v-table table td {
    font-size: 12px;
 }
 .tableHead th{
    background-color: #E3F2FD;
    padding-right:3px !important;
 }

 thead tr th:hover {
  background-color: #BBDEFB !important;
}
 tbody tr:hover {
  background-color: #EEEEEE !important;
}
</style>
