 /* eslint-disable */

 import { createWebHistory, createRouter } from 'vue-router';
 import HomeView from '../src/components/HomeView.vue'
 import HrRules from '../src/views/HrRules.vue'
 import TeamStatus from '../src/views/TeamStatus.vue'
  
 const routes = [
     {
       path: '/',
       name: 'HomeView',
       component: HomeView
     },
     {
        path: '/hrRules',
        name: 'HrRules',
        component: HrRules
      },
      {
        path: '/team-status',
        name: 'TeamStatus',
        component: TeamStatus
      },
      {
        path: "/login",
        name: "Login",
        component: () => import("../src/views/LogIn.vue"),
      },
   ]
 
 const router = createRouter({
   history: createWebHistory("/new.io/"),
   routes,
 });
 
 export default router;
 