<!-- Header.vue -->
<template>
  <div>
    <header class="header">
      <div class="header-top">
        <div class="header-title">
          <router-link to="/">
          한화파워시스템 인사팀
          </router-link>
        </div>
        <button @click="toggleMenu" class="toggle-btn">
          {{ isMenuOpen ? '▲' : '▼' }}
        </button>
      </div>
      <nav v-show="isMenuOpen" class="header-menu">
        <router-link 
          v-for="item in menuItems" 
          :key="item.name" 
          :to="item.route"
        >
          {{ item.name }}
        </router-link>
      </nav>
    </header>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isMenuOpen: true,
      menuItems: [
        { name: 'Home', route: '/' },
        { name: '인사규정', route: '/HrRules' },
        { name: '입사자 메뉴얼', route: '/new-employee-manual' },
        { name: '회사소개', route: '/company-intro' },
        { name: '좌석배치도', route: '/seating-chart' },
        { name: '부서장 메뉴', route: '/Team-status' }
      ]
    }
  },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen
    }
  }
}
</script>

<style scoped>
.header {
  background-color: #333;
  color: white;
}

.header-top {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  position: relative;
}

.header-title {
  font-size: 18px;
  font-weight: bold;
}

.toggle-btn {
  position: absolute;
  right: 16px;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 5px;
}

.header-menu {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  background-color: #444;
}

.header a {
  color: white;
  text-decoration: none;
  padding: 5px 10px;
}

.header-menu a:hover {
  background-color: #555;
}
</style>
